import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';
// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';

// Dialogs

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { useParams } from 'react-router-dom';
import { useRouter } from 'template/routes/hooks';

import {
  // selectors
  selectAccessToken,
} from 'features/user/userSlice';
import {
  selectItemUpdateStatus,
  selectSelectedCategoryList,
  // selectors
  selectSelectedMenu,
  selectSelectedMenuList,
} from 'features/main/mainSlice';

import {
  // selectors
  // main data (၂) မျိုး
  selectItemList,
  selectSelectedItem,
  // CRUD Status (၄) မျိုး
  selectItemStatus,
  selectItemStoreStatus,
  // selectItemUpdateStatus,
  selectItemDeleteStatus,
  // Error (၂) မျိုး
  selectItemError,
  selectItemErrors,

  // actions
  // local state ပြောင်းတာတွေ
  setCategories,
  setSelectedItem,
  updateSelectedItem,
  // local status ပြောင်းတာတွေ
  setItemStatus,
  setItemStoreStatus,
  setItemUpdateStatus,
  setItemDeleteStatus,
  // Validation Errors တွေ set လုပ်ဖို့
  setItemErrors,
} from 'features/main/mainSlice';

import {
  // thunks
  // CRUD Thunks  တွေ။
  selectItemListAsyncThunk,
  storeItemAsyncThunk,
  updateItemAsyncThunk,
  deleteItemAsyncThunk,
} from 'features/item/itemSlice';
import { FormControl, FormHelperText, InputLabel, MenuItem, Select } from '@mui/material';

export default function ItemDetailView() {
  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const router = useRouter();
  const { id } = useParams();

  // global state

  const selectedCategoryList = useSelector(selectSelectedCategoryList);
  const menuListFromWhere = useSelector(selectSelectedMenuList);
  const [menuList, setMenuList] = useState(menuListFromWhere);
  

  // internal state
  // for delete confirmation dialog
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    placement: 0,
    shop_id: 0,
    name: '',
    description: '',
  });

  const [files, setFiles] = useState({
    background_image: null,
    shop_image: null,
  });


  // const [menuList, setMenuList] = useState([]);

  // selector hooks
  // item
  // လိုတာတွေပဲ Select လုပ်မလား?
  // အကုန် ယူမလား?
  // module တစ်ခုမှာ လိုတာတွေပဲယူလိုက်တာ ကောင်းပါတယ်။
  const itemUpdateStatus = useSelector(selectItemUpdateStatus);
  const itemDeleteStatus = useSelector(selectItemDeleteStatus);
  const error = useSelector(selectItemError);
  const errors = useSelector(selectItemErrors);
  const selectedItem = useSelector(selectSelectedItem);
  // ဒါက supllimentry လိုတဲ့ data တွေ။
  // menu , menu dropdown မပေးဘူး။ လက်ရှိ ဝင်ထားတဲ့ Menu အောက် အော်တိုရောက်
  const selectedMenu = useSelector(selectSelectedMenu);
  // user
  // ဒီမှာလည်း
  const accessToken = useSelector(selectAccessToken);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    // if category_id changes, we need to update menuList as new array
    if (name === 'category_id') {
      // update menu list
      setMenuList(selectedCategoryList.find((category) => category.id === value).menus);
    }
    dispatch(updateSelectedItem({ name, value }));
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChanges = (event) => {
    const { name, current_files } = event;
    console.log('Name & Files:', name, current_files);
    setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });
  };

  const handleErrorDialogClose = () => {
    dispatch(setItemDeleteStatus('idle'));
  };

  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();

    // validate form data
    // Validation logic
    const validationErrors = {};

    /*
    if (!formData.name) {
      validationErrors.name = 'Name is required';
    }

    if (!formData.description) {
      validationErrors.description = 'Description is required';
    }

    if (!formData.old_price) {
      validationErrors.old_price = 'Old Price is required';
    }

    if (!formData.current_price) {
      validationErrors.current_price = 'Current Price is required';
    }

    if (!formData.quantity) {
      validationErrors.quantity = 'Quantity is required';
    }

    if (!formData.background_image) {
      // validationErrors.background_image = 'Background Image is required';
    }
    */
    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      // alert("validation error");
      // setErrors(validationErrors);
      dispatch(setItemErrors(validationErrors));
    } else {
      dispatch(setItemErrors({}));
      const data = new FormData(event.target);
      // check local file and add to form data
      if (files['background_image'] !== undefined) {
        data.append('background_image', files['background_image']);
      }

      dispatch(updateItemAsyncThunk(data));
    }
  };

  /*
  const handleClose = () => {
    dispatch(setItemUpdateStatus('idle'));
    setOpen(false);
  };
  */

  const handleClose = (id, access_token) => {
    dispatch(setItemUpdateStatus('idle'));
    console.log('id is ' + id);
    console.log('access_token is ' + access_token);
    if (id !== 0) {
      // dispatch delete shop api
      let formData = new FormData();
      formData.append('access_token', access_token);
      formData.append('id', id);
      dispatch(deleteItemAsyncThunk(formData));
      //dispatch(deleteShopAsyncThunk(formData));
    }
    setOpen(false);
  };

  useEffect(() => {
    if (itemUpdateStatus === 'succeeded') {
      // need to dispatch to set status as idle
      dispatch(setItemUpdateStatus('idle'));
      // should go back to get to shop detail
      // navigate("/shops");
      navigate(-1); // This is equivalent to pressing the back button
    }
  }, [itemUpdateStatus]); // Include history as a dependency

  useEffect(() => {
    if (itemDeleteStatus === 'succeeded') {
      // set back to idle
      dispatch(setItemDeleteStatus('idle'));
      // and then go back to shops detail
      router.back();
    }
  }, [itemDeleteStatus]); // Include history as a dependency

  /*
    တကယ်လိုတဲ့ Field က Name, Description, Image
    Optional က placement
  */
  return (
    <>
      <Container component="main" maxWidth="xs">
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5">
            Item Detail
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <input
              type="hidden"
              value={accessToken}
              name="access_token"
              placeholder="Hidden Input"
            />
            <input
              type="hidden"
              value={selectedItem.menu_id}
              name="menu_id"
              placeholder="Hidden Input for Selected Menu Id"
            />
            <input
              type="hidden"
              value={selectedItem.id}
              name="id"
              placeholder="Hidden Input for Selected Menu Id"
            />
            <input type="hidden" value={0} name="placement" placeholder="Item Placement" />
            <input type="hidden" value={1} name="is_active" placeholder="Active Flag" />
            <input type="hidden" value={0} name="visit_count" placeholder="Visit Count" />
            <input type="hidden" value={0} name="old_price" placeholder="Old Price" />

            {/* category list dropdown */}
            <FormControl fullWidth>
              <InputLabel id="category-select">Category</InputLabel>
              <Select
                autoFocus
                labelId="category-select"
                id="category-select"
                value={formData.category_id === undefined ? selectedItem.menu.category_id : formData.category_id }
                label="Category"
                name="category_id"
                onChange={handleChange}
                error={errors.hasOwnProperty('category_id')}
                helperText={errors.category_id}
              >
                {selectedCategoryList.map((category) => (
                  <MenuItem key={category.id} value={category.id}>{category.name}</MenuItem>
                ))}
                {/* <MenuItem value={10}>Ten</MenuItem>
                <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
              </Select>
              <FormHelperText sx={{ color: 'red' }}>{errors.category_id}</FormHelperText>
            </FormControl>



            <br></br>
            <br></br>

            {/* { menuList.length } */}
            {/* menu list dropdown */}
            <FormControl fullWidth>
              <InputLabel id="menu-select">Menu</InputLabel>
              <Select
                autoFocus
                labelId="menu-select"
                id="menu-select"
                value={formData.menu_id === undefined ? selectedItem.menu_id : formData.menu_id }
                label="Menu"
                name="menu_id"
                onChange={handleChange}
                error={errors.hasOwnProperty('menu_id')}
                helperText={errors.menu_id}
              >
                {menuList.map((menu) => (
                  <MenuItem value={menu.id}>{menu.name}</MenuItem>
                ))}
                {/* <MenuItem value={10}>Ten</MenuItem>
                <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
              </Select>
              <FormHelperText sx={{ color: 'red' }}>{errors.menu_id}</FormHelperText>
            </FormControl>


            {/* name */}
            <TextField
              id="name"
              label="Name"
              name="name"
              value={selectedItem.name ?? ''}
              onChange={handleChange}
              error={errors.hasOwnProperty('name')}
              helperText={errors.name}
              autoComplete="name"
              margin="normal"
              required
              fullWidth
              autoFocus
            />

            {/* description */}
            <TextField
              multiline
              maxRows={3}
              minRows={3}
              id="description"
              label="Recipe's Detail"
              name="description"
              value={selectedItem.description ?? ''}
              autoComplete="description"
              onChange={handleChange}
              error={errors.hasOwnProperty('description')}
              helperText={errors.description}
              margin="normal"
              required
              fullWidth
            />

            {/* old price */}
            {/* <TextField
              name="old_price"
              type="number"
              id="old_price"
              value={selectedItem.old_price ?? ''}
              label="Old Price"
              autoComplete="description"
              onChange={handleChange}
              error={errors.hasOwnProperty('old_price')}
              helperText={errors.old_price}
              margin="normal"
              required
              fullWidth
            /> */}

            {/* current price */}
            <TextField
              name="current_price"
              type="number"
              id="current_price"
              value={selectedItem.current_price ?? ''}
              label="Price"
              autoComplete="current_price"
              onChange={handleChange}
              error={errors.hasOwnProperty('current_price')}
              helperText={errors.current_price}
              margin="normal"
              required
              fullWidth
            />

            {/* quantity  */}
            {/* quantity  */}
            <br></br>
            <br></br>
            <FormControl fullWidth>
              <InputLabel id="quantity">Stock Status</InputLabel>
              <Select
                labelId="quantity"
                id="quantity"
                value={formData.quantity === undefined ? selectedItem.quantity : formData.undefined}
                label="Stock Status"
                name="quantity"
                onChange={handleChange}
                error={errors.hasOwnProperty('quantity')}
                helperText={errors.quantity}
              >
                <MenuItem value={0}>Out of Stock</MenuItem>
                <MenuItem value={1}>In Stock</MenuItem>
              </Select>
              <FormHelperText sx={{ color: 'red' }}>{errors.category_id}</FormHelperText>
            </FormControl>
            {/* <TextField
              name="quantity"
              type="number"
              id="quantity"
              value={selectedItem.quantity ?? ''}
              label="Quantity"
              autoComplete="quantity"
              onChange={handleChange}
              error={errors.hasOwnProperty('quantity')}
              helperText={errors.quantity}
              margin="normal"
              required
              fullWidth
            /> */}

            <Typography>Item Image {files['background_image']?.name}</Typography>
            <img src={selectedItem.background_image_url} />

            <TextField
              type="file"
              name="background_image"
              onChange={handleChange}
              error={errors.hasOwnProperty('background_image')}
              helperText={errors.background_image}
              fullWidth
            />

            <Box sx={{ m: 1, position: 'relative' }}>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                disabled={itemUpdateStatus === 'loading'}
              >
                Update Menu
              </Button>
              {itemUpdateStatus === 'loading' && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: blue[500],
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    marginTop: '-12px',
                    marginLeft: '-12px',
                  }}
                />
              )}
            </Box>
          </Box>

          {/* <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
            onClick={() => setOpen(true)}
            disabled={itemUpdateStatus === 'loading'}
          >
            Delete Menu
          </Button> */}
        </Box>

        {/* error dialog */}
        <Dialog
          open={itemUpdateStatus === 'error'}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{'Error'}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} autoFocus>
              OK
            </Button>
          </DialogActions>
        </Dialog>

        {/* delete confirmation dialog */}
        <Dialog
          open={open}
          // onClose={() => handleClose(id)}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">Delete Confirmation!</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              Are you sure to delete this category info?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose(0)} autoFocus>
              Cancel
            </Button>
            <Button onClick={() => handleClose(id, accessToken)} autoFocus>
              OK
            </Button>
          </DialogActions>
        </Dialog>

        {/* delete error dialog */}
        <Dialog
          open={itemDeleteStatus === 'error'}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">Error!</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleErrorDialogClose} autoFocus>
              OK
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </>
  );
}
